* {
    box-sizing: border-box;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
        Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #fafafa;
    color: #212121;
    line-height: 1.5;
}

#search-box {
    font: inherit;
    padding: 8px;
    max-width: 320px;
    margin-bottom: 16px;
}

/* countries */

.countries-list {
    width: 300px;
}

.countries-list__item {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.countries-list__flag {
    margin-right: 10px;
}

.countries-list__text {
    font-size: 20px;
}
/* country */
.country-details {
    width: 500px;
    display: flex;
    flex-direction: column;
}

.country-details__header {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.country-details__title {
    display: inline-block;
    margin-left: 20px;

    font-size: 30px;
}

.country-details__item {
    font-size: 20px;
}

.country-details__descr-title {
    text-transform: capitalize;
    font-weight: 500;
}

.country-details__lang-text {
    display: inline-block;
}
